.mapTransport {
  position: relative;
  min-height: 107rem;

  &__title {
    position: relative;
    z-index: 1;
    padding: 0 0 0 13rem;
  }

  &__map {
    position: absolute;
    top: 6rem;
    left: -12rem;
  }

  &__mapPart {
    position: absolute;
    z-index: 1;
    opacity: 0;
    transition: opacity $eia250;

    &--big {
      top: 23rem;
      left: 6rem;
    }

    &--small {
      top: 28.6rem;
      left: 62.6rem;
    }

    &--percentage {
      bottom: 10rem;
      width: 192rem;
      left: -12rem;
      opacity: 1;
    }

    &.active {
      opacity: 1;
    }
  }

  &__toggle {
    position: absolute;
    z-index: 2;
    top: 38rem;
    left: 104rem;
    width: 9rem;
    height: 26rem;
    background-color: transparent;
  }
}