.header {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  right: 0;
  min-height: 14.5rem;
  backdrop-filter: blur(30px);
  background-color: rgba($white, .4);

  &__content {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 1.5rem 4rem;
    @include contentWidth;
  }

  &__logo-wrapper {
    &.active {
      pointer-events: none;
    }
  }

  &__logo {
    width: 40rem;
    height: 9rem;
    margin: 0 0 2rem;
  }


  &__nav-box {
    display: flex;
    justify-content: space-between;
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5rem;
  }


  &__nav-item {
    position: relative;
    margin: 0 0 0 3.4rem;
    transition: color $eia200;

    &:hover,
    &.active {
      color: $purple;
    }

    &.active {
      cursor: default;
    }
  }


  &__nav-value {
    .header__nav-item.active & {
      pointer-events: none;
    }
  }

  &__toggle {
    position: absolute;
    top: 1.5rem;
    left: .5rem;
    display: block;
    width: 2rem;
    height: 2rem;
    border-top: .2rem solid $black;
    border-left: .2rem solid $black;
    transform: rotate(-135deg);
    opacity: 1;
    transition: opacity $eia250;

    &.opened {
      opacity: 0;
      pointer-events: none;
    }
  }
}

@media (max-width: 1680px) {
  .header {
    &__content {
      padding: 3rem 1.5rem 0;
    }
  }
}