.tabs {
  position: relative;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  background-color: $gray4;
  border-radius: .5rem;

  &.link {
    background-color: transparent;
    border-radius: 0;
  }

  &__item {
    min-height: 2.8rem;
    padding: 0 2rem;
    font-size: $fz-m;
    line-height: 2rem;

    &.active {
      background-color: $black;
      color: $white;
      border-radius: .5rem;
      transition: color $eia200;

      .link & {
        color: $blue;
        background-color: transparent;
        border-radius: 0;
      }
    }
  }

  &__hint {
    justify-self: flex-start;
    padding: 0 0 0 13rem;
    font-size: $fz-xs;
    line-height: 1.7rem;
    color: $gray2;
  }
}

@media (max-width: 1680px) {
  .tabs {
    &__item {
      padding: 0 1rem;
    }

    &__hint {
      padding: 0;
    }
  }
}